import React, { useState } from "react"
import { Sun, Moon } from "@phosphor-icons/react"
import { useTheme } from "./ThemeProvider"

export function ThemeToggle(): React.ReactElement {
  const { isDark, toggleTheme } = useTheme()
  const [isRotating, setIsRotating] = useState(false)

  const handleClick = () => {
    setIsRotating(true)
    toggleTheme()
    // Reset rotation state after animation completes
    setTimeout(() => setIsRotating(false), 300)
  }

  return (
    <button
      onClick={handleClick}
      className={`
        relative inline-flex items-center justify-center
        w-10 h-10 rounded-lg
        bg-background border border-border
        text-foreground
        hover:border-primary hover:text-primary
        transition-all duration-200
        focus:outline-none focus:ring-2 focus:ring-primary/50
        cursor-pointer
      `}
      aria-label={isDark ? "" : ""}
      title={isDark ? "" : ""}
    >
      <span
        className={`
          inline-block transition-transform duration-300 ease-in-out
          ${isRotating ? "rotate-180" : "rotate-0"}
        `}
      >
        {isDark ? (
          <Sun weight="duotone" size={20} className="text-primary" aria-hidden="true" />
        ) : (
          <Moon weight="duotone" size={20} className="text-primary" aria-hidden="true" />
        )}
      </span>
    </button>
  )
}

export default ThemeToggle
