import React, { useState, useEffect } from "react"
import { graphql, Link, StaticQuery } from "gatsby"
import { GatsbyImage } from "gatsby-plugin-image"
import { List, X } from "@phosphor-icons/react"
import { ThemeToggle } from "../theme"

const navLinks = [
  { to: "/", label: "Home" },
  { to: "/works/", label: "Work" },
  { to: "/think/", label: "Think" },
  { to: "/tags/", label: "Tags" },
  { to: "/about/", label: "About" },
  { href: "https://github.com/ximing", label: "GitHub", external: true },
]

interface HeaderData {
  avatar: {
    childImageSharp: {
      gatsbyImageData: any
    }
  }
  site: {
    siteMetadata: {
      author: string
    }
  }
}

function HeaderContent({ data }: { data: HeaderData }): React.ReactElement {
  const { author } = data.site.siteMetadata
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const [isScrolled, setIsScrolled] = useState(false)

  // Handle scroll effect for header shadow
  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 10)
    }
    window.addEventListener("scroll", handleScroll, { passive: true })
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  // Prevent body scroll when mobile menu is open
  useEffect(() => {
    if (isMobileMenuOpen) {
      document.body.style.overflow = "hidden"
    } else {
      document.body.style.overflow = "unset"
    }
    return () => {
      document.body.style.overflow = "unset"
    }
  }, [isMobileMenuOpen])

  const toggleMobileMenu = () => setIsMobileMenuOpen(!isMobileMenuOpen)
  const closeMobileMenu = () => setIsMobileMenuOpen(false)

  return (
    <>
      <header
        className={`sticky top-0 z-50 w-full border-0 backdrop-blur-md transition-shadow duration-300 ${
          isScrolled
            ? "bg-background/95 shadow-sm"
            : "bg-background/80"
        }`}
      >
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            {/* Left: Logo + Blog Title */}
            <Link
              to="/"
              className="flex items-center gap-3 text-accent hover:text-accent-hover transition-colors group"
            >
              <GatsbyImage
                image={data.avatar.childImageSharp.gatsbyImageData}
                alt={author}
                className="w-10 h-10 rounded-full ring-2 ring-accent/20 group-hover:ring-accent/40 transition-all"
                imgStyle={{ borderRadius: "50%" }}
              />
              <span className="font-semibold text-lg hidden sm:block font-serif">
                {author}
              </span>
            </Link>

            {/* Right: Navigation Links + Theme Toggle + Mobile Menu Button */}
            <div className="flex items-center gap-4">
              {/* Desktop Navigation */}
              <nav className="hidden md:flex items-center gap-6">
                {navLinks.map((link) =>
                  link.external ? (
                    <a
                      key={link.href}
                      href={link.href}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="nav-link py-2 text-sm font-medium"
                    >
                      {link.label}
                    </a>
                  ) : (
                    <Link
                      key={link.to}
                      to={link.to}
                      className="nav-link py-2 text-sm font-medium"
                      activeClassName="nav-link-active"
                      partiallyActive={link.to !== "/"}
                    >
                      {link.label}
                    </Link>
                  )
                )}
              </nav>

              {/* Theme Toggle */}
              <ThemeToggle />

              {/* Mobile Menu Button */}
              <button
                onClick={toggleMobileMenu}
                className="md:hidden relative inline-flex items-center justify-center w-10 h-10 rounded-lg bg-background border border-border text-foreground hover:border-primary hover:text-primary transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-primary/50"
                aria-label={isMobileMenuOpen ? "" : ""}
                aria-expanded={isMobileMenuOpen}
                aria-controls="mobile-menu"
              >
                {isMobileMenuOpen ? (
                  <X weight="duotone" size={20} className="text-foreground" aria-hidden="true" />
                ) : (
                  <List weight="duotone" size={20} className="text-foreground" aria-hidden="true" />
                )}
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Mobile Menu Curtain */}
      <div
        id="mobile-menu"
        className={`fixed inset-x-0 top-0 z-40 md:hidden ${
          isMobileMenuOpen ? "pointer-events-auto" : "pointer-events-none"
        }`}
        role="dialog"
        aria-modal="true"
        aria-label=""
      >
        {/* Backdrop */}
        <div
          className={`absolute inset-0 bg-background/95 backdrop-blur-lg transition-opacity duration-300 ${
            isMobileMenuOpen ? "opacity-100" : "opacity-0"
          }`}
          onClick={closeMobileMenu}
          aria-hidden="true"
          style={{ height: "100vh" }}
        />

        {/* Curtain Content */}
        <div
          className={`relative bg-background border-b border-border shadow-xl transition-all duration-300 ease-out ${
            isMobileMenuOpen
              ? "translate-y-0 opacity-100"
              : "-translate-y-full opacity-0"
          }`}
        >
          <div className="pt-20 pb-8 px-6">
            {/* Close Button */}
            <button
              onClick={closeMobileMenu}
              className="absolute top-4 right-4 inline-flex items-center justify-center w-10 h-10 rounded-lg bg-background border border-border text-foreground hover:border-accent hover:text-accent transition-all duration-200"
              aria-label=""
            >
              <X weight="duotone" size={20} aria-hidden="true" />
            </button>

            {/* Navigation Links */}
            <nav className="space-y-2">
              {navLinks.map((link, index) =>
                link.external ? (
                  <a
                    key={link.href}
                    href={link.href}
                    target="_blank"
                    rel="noopener noreferrer"
                    onClick={closeMobileMenu}
                    className={`block px-4 py-3 text-lg font-medium text-foreground-secondary hover:text-accent hover:bg-accent-subtle rounded-lg transition-all duration-200 ${
                      isMobileMenuOpen ? "translate-y-0 opacity-100" : "-translate-y-4 opacity-0"
                    }`}
                    style={{
                      transitionDelay: isMobileMenuOpen ? `${index * 75}ms` : "0ms",
                    }}
                  >
                    {link.label}
                  </a>
                ) : (
                  <Link
                    key={link.to}
                    to={link.to}
                    onClick={closeMobileMenu}
                    className={`block px-4 py-3 text-lg font-medium text-foreground-secondary hover:text-accent hover:bg-accent-subtle rounded-lg transition-all duration-200 ${
                      isMobileMenuOpen ? "translate-y-0 opacity-100" : "-translate-y-4 opacity-0"
                    }`}
                    activeClassName="text-accent bg-accent-subtle"
                    style={{
                      transitionDelay: isMobileMenuOpen ? `${index * 75}ms` : "0ms",
                    }}
                  >
                    {link.label}
                  </Link>
                )
              )}
            </nav>

            {/* Decorative Element */}
            <div className="mt-8 pt-6 border-t border-border">
              <p className="text-sm text-foreground-muted text-center font-serif italic">
                Digital Garden - 
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

function Header(): React.ReactElement {
  return (
    <StaticQuery
      query={headerQuery}
      render={(data: HeaderData) => <HeaderContent data={data} />}
    />
  )
}

const headerQuery = graphql`
  query LayoutHeaderQuery {
    avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
      childImageSharp {
        gatsbyImageData(layout: FIXED, width: 40, height: 40, formats: [JPG])
      }
    }
    site {
      siteMetadata {
        author
      }
    }
  }
`

export default Header
