import React, {
  createContext,
  useContext,
  useEffect,
  useState,
  useCallback,
} from "react"

export type Theme = "light" | "dark"

interface ThemeContextType {
  theme: Theme
  setTheme: (theme: Theme) => void
  toggleTheme: () => void
  isDark: boolean
}

/**
 * Send theme change message to Giscus iframe
 * This allows Giscus to update its theme without reloading
 */
function sendGiscusMessage(theme: Theme) {
  if (typeof window === "undefined") return

  const iframe = document.querySelector<HTMLIFrameElement>("iframe.giscus-frame")
  if (!iframe) return

  const giscusTheme = theme === "dark" ? "dark" : "light"

  iframe.contentWindow?.postMessage(
    {
      giscus: {
        setConfig: {
          theme: giscusTheme,
        },
      },
    },
    "https://giscus.app"
  )
}

const ThemeContext = createContext<ThemeContextType | undefined>(undefined)

const STORAGE_KEY = "blog-theme"

function getInitialTheme(): Theme {
  // Check if we're in a browser environment
  if (typeof window === "undefined") {
    return "light"
  }

  // Check localStorage first
  const stored = localStorage.getItem(STORAGE_KEY) as Theme | null
  if (stored && (stored === "light" || stored === "dark")) {
    return stored
  }

  // Fall back to system preference
  if (window.matchMedia) {
    const prefersDark = window.matchMedia(
      "(prefers-color-scheme: dark)"
    ).matches
    return prefersDark ? "dark" : "light"
  }

  return "light"
}

interface ThemeProviderProps {
  children: React.ReactNode
}

export function ThemeProvider({ children }: ThemeProviderProps) {
  const [theme, setThemeState] = useState<Theme>("light")
  const [mounted, setMounted] = useState(false)

  // Initialize theme after mount to avoid hydration mismatch
  useEffect(() => {
    setThemeState(getInitialTheme())
    setMounted(true)
  }, [])

  // Apply theme to document when it changes
  useEffect(() => {
    if (!mounted) return

    const root = document.documentElement

    if (theme === "dark") {
      root.setAttribute("data-theme", "dark")
    } else {
      root.removeAttribute("data-theme")
    }

    // Persist to localStorage
    localStorage.setItem(STORAGE_KEY, theme)

    // Sync Giscus theme
    sendGiscusMessage(theme)
  }, [theme, mounted])

  // Listen to system theme changes
  useEffect(() => {
    if (!mounted) return

    const mediaQuery = window.matchMedia("(prefers-color-scheme: dark)")

    const handleChange = (e: MediaQueryListEvent) => {
      // Only apply system preference if user hasn't explicitly set a preference
      const stored = localStorage.getItem(STORAGE_KEY)
      if (!stored) {
        setThemeState(e.matches ? "dark" : "light")
      }
    }

    mediaQuery.addEventListener("change", handleChange)
    return () => mediaQuery.removeEventListener("change", handleChange)
  }, [mounted])

  const setTheme = useCallback((newTheme: Theme) => {
    setThemeState(newTheme)
  }, [])

  const toggleTheme = useCallback(() => {
    setThemeState((prev) => (prev === "light" ? "dark" : "light"))
  }, [])

  const value: ThemeContextType = {
    theme,
    setTheme,
    toggleTheme,
    isDark: theme === "dark",
  }

  return (
    <ThemeContext.Provider value={value}>{children}</ThemeContext.Provider>
  )
}

export function useTheme(): ThemeContextType {
  const context = useContext(ThemeContext)
  if (context === undefined) {
    throw new Error("useTheme must be used within a ThemeProvider")
  }
  return context
}

export default ThemeProvider
